import React, { useState, useEffect } from "react";
import '../../App.css';
import { BiZoomIn, BiZoomOut } from 'react-icons/bi'; // Importowanie ikon


function Home() {
  const [scale, setScale] = useState(1); // Poziom powiększenia
  const [imageUrl, setImageUrl] = useState(null); // URL obrazu
  const [lastTouchDistance, setLastTouchDistance] = useState(0); // Odległość między dwoma punktami dotyku
  const [azimuth, setAzimuth] = useState(0); // Azymut w stopniach (kąt względem północy)
  const [viewPosition, setViewPosition] = useState({ x: 0, y: 0 }); // Pozycja widoku
  const [lastTouchPosition, setLastTouchPosition] = useState(null); // Pozycja palca na początku przesuwania

  useEffect(() => {
    fetch("/config.json")
      .then((response) => response.json())
      .then((data) => setImageUrl(data.imageUrl))
      .catch((error) => console.error("Błąd ładowania config.json:", error));
  }, []);

  useEffect(() => {
    const handleOrientation = (event) => {
      const alpha = event.alpha || 0;
      setAzimuth(alpha);
    };
    window.addEventListener("deviceorientation", handleOrientation);
    return () => window.removeEventListener("deviceorientation", handleOrientation);
  }, []);

  // Funkcja obliczająca odległość między dwoma punktami dotyku
  const getTouchDistance = (e) => {
    const dx = e.touches[0].clientX - e.touches[1].clientX;
    const dy = e.touches[0].clientY - e.touches[1].clientY;
    return Math.sqrt(dx * dx + dy * dy);
  };

  // Obsługa dotyku na początku
  const handleTouchStart = (e) => {
    if (e.touches.length === 2) {
      const touchDistance = getTouchDistance(e);
      setLastTouchDistance(touchDistance);
    }
    if (e.touches.length === 1) {
      setLastTouchPosition({
        x: e.touches[0].clientX,
        y: e.touches[0].clientY,
      });
    }
  };

  const handleTouchMove = (e) => {
    if (e.touches.length === 2) {
      const touchDistance = getTouchDistance(e);
      const scaleFactor = touchDistance / lastTouchDistance;
      const newScale = Math.min(Math.max(scale * scaleFactor, 1), 3); // Zoom od x1 do x3
      setScale(newScale);
      setLastTouchDistance(touchDistance);
    }

    if (e.touches.length === 1 && lastTouchPosition) {
      const dx = e.touches[0].clientX - lastTouchPosition.x;
      const dy = e.touches[0].clientY - lastTouchPosition.y;

      // Obliczanie granic dla przesuwania
      const maxX = (scale - 1) * window.innerWidth;
      const maxY = (scale - 1) * window.innerHeight;

      const newViewPosition = {
        x: Math.min(Math.max(viewPosition.x + dx, -maxX), maxX), // Ograniczamy przesunięcie w poziomie
        y: Math.min(Math.max(viewPosition.y + dy, -maxY), maxY), // Ograniczamy przesunięcie w pionie
      };

      setViewPosition(newViewPosition);
      setLastTouchPosition({
        x: e.touches[0].clientX,
        y: e.touches[0].clientY,
      });
    }
  };

  const handleTouchEnd = () => {
    setLastTouchPosition(null);
  };


  // Funkcje do obsługi przycisków zoomu
  const handleZoomIn = () => setScale((prev) => Math.min(prev + 0.1, 3));
  const handleZoomOut = () => setScale((prev) => Math.max(prev - 0.1, 1));

  return (
    <div
      style={{
        position: "relative",
        width: "100vw",
        height: "100vh",
        overflow: "hidden",
        touchAction: "none", // Wyłącza domyślne zachowanie dotyku (np. przewijanie)
      }}
      onTouchStart={handleTouchStart}
      onTouchMove={handleTouchMove}
      onTouchEnd={handleTouchEnd}
    >
      {/* Obrazek mapy */}
      {imageUrl && (
        <div
          className="background"
          style={{
            transform: `scale(${scale}) translate(${viewPosition.x}px, ${viewPosition.y}px)`,
            transition: "transform 0.1s", // Płynne przejście powiększenia
            width: "100%", // Dostosowanie do szerokości ekranu
            height: "100%", // Dostosowanie do wysokości ekranu
          }}
        >
          <img src={imageUrl} alt="mapa suliszowice" className="background-image" />
        </div>
      )}

      {/* Kompas */}
      <div
        className="compass-container"
        style={{
          position: "fixed",
          top: "20px",
          right: "20px",
          zIndex: 999,
          pointerEvents: "none",
          display: "flex",
          flexDirection: "column",
          alignItems: "center",
        }}
      >
        <img
          src={"/Assets/elo.png"}
          alt="Kompas"
          className="compass-icon"
          style={{
            width: "100px",
            height: "100px",
            transform: `rotate(${-azimuth}deg)`, // Obrót kompasu w zależności od azymutu
          }}
        />
        <span className="azimuth-value" style={{ marginTop: "5px", color: "black", fontFamily: "Poppins, sans-serif" }}>
          {Math.round(azimuth)}°
        </span>
      </div>

      {/* Przyciski zoomu */}
      <div className="zoom-controls" style={{ position: "fixed", bottom: "20px", right: "35px" }}>
        <button
          onClick={handleZoomIn}
        >
          <BiZoomIn />
        </button>
        <button
          onClick={handleZoomOut}

        >
          <BiZoomOut />
        </button>
      </div>
    </div>
  );
}

export default Home;
